import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { getDataSimple } from '../redux/actions/table'
import * as tableActions from '../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom'
import { mainWindow } from '../App'
import { getHalls } from '../component/CinemaHall/index'
import Render from '../component/CinemaHall/Render/Render'

const width = mainWindow.current.offsetWidth>800? 500: 240;

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    image: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        maxWidth: 500,
        maxHeight: 500
    },
    message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: 'calc((100% - '+width+'px)/2)',
        marginRight: 'calc((100% - '+width+'px)/2)'
    },
});

const Cashbox = (
    (props) =>{
        const { setSelected, addData, setData } = props.tableActions;
        const { selected, data } = props.table;
        const { classes } = props;
        const { status } = props.user;

        useEffect(async()=>{
            if (!(status.status==='active'&&['admin', 'manager', 'cashier', 'cinema'].includes(status.role))) {
                props.history.push('/')
            }
            await setUsers(await getDataSimple({name: 'Кинотеатры'}));
            await setMovies(await getDataSimple({name: 'ВсеКино'}));
        },[])
        let [movies, setMovies] = useState([]);
        let [movie, setMovie] = useState('');
        let handleMovie = (event) => {
            setMovie(event.target.value)
            let name = ''
            for(let i=0; i<movies.length; i++){
                if (movies[i]._id===event.target.value)
                    name = movies[i].name
            }
            setNameMovie(name)
            setHalls([])
            handleHall({target: {value: ''}})
            setUser('')
        };
        let [user, setUser] = useState('');
        let handleUser = (event) => {
            setUser(event.target.value)
            let name = ''
            for(let i=0; i<users.length; i++){
                if (users[i]._id===event.target.value)
                    name = users[i].name
            }
            setNameCinema(name)
            setHalls(getHalls(name))
            handleHall({target: {value: ''}})
        };
        let [halls, setHalls] = useState([]);
        let [hall, setHall] = useState('');
        let handleHall = (event) => {
            setHall(event.target.value)
            if(event.target.value==='') {
                setDates([])
                handleDate({target: {value: ''}})
            }
        };
        let [dates, setDates] = useState([]);
        let [date, setDate] = useState('');
        let handleDate = (event) => {
            setDate(event.target.value)
        };
        let [seance, setSeance] = useState(undefined);
        let [users, setUsers] = useState([]);
        let [nameMovie, setNameMovie] = useState('');
        let [nameCinema, setNameCinema] = useState('');
        let [items, setItems] = useState([]);
        let addItem = (item) => {
            let _items = items;
            let index = -1;
            item.date=date.replace('-', '.').replace('-', '.').replace('T', ' ').replace(':00.000Z', '')
            _items.find((element, position)=>{
                if(JSON.stringify(element)===JSON.stringify(item))
                    index = position
            });
            if (index===-1)
                _items.push(item);
            else
                _items.splice(index, 1);
            setItems(_items)
        }
        let setSeats = (seats) => {
            let _seance = seance
            _seance.seats = seats
            setSeance(_seance)
        }
        useEffect(async()=>{
            setDates(await getDataSimple({name: 'СеансДаты', data: {user: nameCinema, movie: movie}}));
            setDate('')
            setItems([])
        },[nameCinema])
        useEffect(async()=>{
            if(date!==''){
                setSeance(await getDataSimple({name: 'СеансПоДате', data: {realDate: date, user: nameCinema+'|'+hall, movie: movie}}));
            } else {
                setSeance(undefined)
                setItems([])
            }
        },[hall])
        return (
            <div>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='фильм'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={movie}
                    onChange={handleMovie}
                >
                    {movies != undefined?
                        movies.map(option => (
                            <MenuItem key={option._id} value={option._id}>
                                {option.name}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='кинотеатр'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={user}
                    onChange={handleUser}
                >
                    {users != undefined?
                        users.map(option => (
                            <MenuItem key={option._id} value={option._id}>
                                {option.name}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='дата'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={date}
                    onChange={handleDate}
                >
                    {dates != undefined?
                        dates.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='зал'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={hall}
                    onChange={handleHall}
                >
                    {halls != undefined?
                        halls.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <div className={classes.message}>
                {seance != undefined?
                    <>
                    <b>Тип</b>
                    <br/>
                    <div className={classes.message}>
                        {seance.type}
                    </div>
                    <br/>
                    <br/>
                    <Render seats={seance.seats} cashbox={true} index={selected} price={seance.price} setSeats={setSeats} addItem={addItem}/>
                    <br/>
                    </>
                    :
                    null
                }
                </div>
                <br/>
                <br/>
                {items.length>0?
                    items.map(option => {
                        return(
                            <>
                            Место:&nbsp;{option.name}&nbsp;&nbsp;Цена:&nbsp;{option.priceSelect}&nbsp;сом&nbsp;&nbsp;Дата:&nbsp;{option.date}
                            <br/>
                            </>)
                    }):null}
                <br/>
                <div>
                    <Link className='link' to={''} onClick={()=>{
                        if(items.length>0) {
                            let _seance = seance;
                            for (let i = 0; i < _seance.seats.length; i++) {
                                for (let i1 = 0; i1 < _seance.seats[i].length; i1++) {
                                    if(_seance.seats[i][i1].status==='hold')
                                        _seance.seats[i][i1].status='sold'
                                    }
                                }
                            addData({search: '', sort: '', page: 0, name: 'Билеты кино', data: {user: status.id, movie: nameMovie,
                                cinema: nameCinema,
                                seance: seance,
                                image: '',
                                hall: hall, status: 'продан', seats: items}});
                            setSelected(-1)
                        }
                    }}>
                        <Button variant='contained' color='primary' className={classes.button}>
                            Купить
                        </Button>
                    </Link>
                    <Link className='link' to={''} onClick={()=>{setSelected(-1);}}>
                        <Button variant='contained' color='secondary' className={classes.button}>
                            Отмена
                        </Button>
                    </Link>
                </div>
            </div>
        );
  })

function mapStateToProps (state) {
    return {
        table: state.table,
        user: state.user,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Cashbox.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Cashbox));
